/* -----------------------------------------------------------------------
## Event #################################################################
----------------------------------------------------------------------- */
#event {
	.visual {
		margin-top: 32px;
		padding: 0 32px;
		img {
			width: 100%;
			height: auto;
		}
		&_copy {
			margin-top: 16px;
			font-size: 1.5rem;
		}
		&_text {
			margin-top: 16px;
		}
	}
}

@media screen and (max-width: 768px){
	#event {
		.sectionInner {
			padding: 16px 10px 32px;
		}
	}
}