/* -----------------------------------------------------------------------
## mainVisual ############################################################
----------------------------------------------------------------------- */
#mainVisual {
	padding-bottom: 123px;
	.sectionInner {
		padding: 32px;
		background: transparent;
	}
	.siteTitle {
		width: 400px;
		margin: 64px auto 0;
	}
	.actions {
		margin: 64px 0 15px;
		text-align: center;
		&_item {
			display: inline-block;
			margin: 0 10px;
			a {
				position: relative;
				display: inline-block;
				line-height: 44px;
				padding: 0 40px 0 20px;
				border-radius: 1000px;
				background: $color_bg_light;
				color: $color_text_dark;
				@include fontsize(20);
				&:after {
					display: block;
					content: "\f054";
					position: absolute;
					top: 0;
					bottom: 0;
					right: 10px;
					height: 44px;
					margin: auto;
					@include fontsize(16);
					font-family: FontAwesome;
				}
			}
		}
	}
	.bnrs {
		margin: 64px 0 0;
		li {
			margin-top: 16px;
		}
		a {
			display: block;
		}
	}
	.news {
		position: absolute;
		bottom: 0;
		width: 100%;
		z-index: 1;
		background: rgba(0,0,0,.75);
		padding: 0 0 16px;
		&_inner {
			width: 1000px;
			margin: 0 auto;
		}
		&List {
			font: inherit;
			&_item {
				display: none;
				background: transparent;
				line-height: 27px;
				margin-left: 100px;
			}

		}
		.tickercontainer {
			width: 100%;
			background: transparent;
			border: none;
			.mask {
				top: 0;
				width: 100%;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	#mainVisual {
		.sectionInner {
			padding: 16px 0;
		}
		.siteTitle {
			width: auto;
			max-width: 400px;
			margin: 0 auto;
			padding: 0 10px;
		}
		.siteTitle img {
			width: 100%;
			height: auto;
		}
		.actions {
			margin: 0;
			&_item {
				width: 60%;
				max-width: 400px;
				margin-top: 16px;
			}
			&_item a {
				width: 100%;
			}
		}
		.snsBtns {
			margin: 16px 0 0;
		}
		.bnrs {
			margin-top: 16px;
			padding: 0 10px;
			li {
				margin-top: 8px;
			}
			img {
				width: 100%;
			}
		}
		.newsTicker {
			width: auto;
		}
	}
}