/* -----------------------------------------------------------------------
## Map ###################################################################
----------------------------------------------------------------------- */
#map {
	.mapWrapper {
		position: relative;
		width: 100%;
		height: 436px;
		margin-top: 32px;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
	.address {
		margin-top: 16px;
		text-align: center;
		font-size: 1.5rem;
	}
	.navi {
		display: flex;
		justify-content: center;
		align-items: center;
		&_item {
			margin-top: 32px;
			padding: 0 64px;
			text-align: center;
			font-size: 1.5rem;
		}
	}
}
@media screen and (max-width: 768px){
	#map {
		.sectionInner {
			padding: 10px;
		}
		.mapWrapper {
			margin-top: 16px;
		}
		.address {
			@include fontsize(16);
		}
		.navi {
			&_item {
				@include fontsize(16);
				width: 100%;
				margin-top: 8px;
				padding: 0;
			}
			a {
				line-height: 44px;
				padding: 8px;
			}
		}
	}
}