/* -----------------------------------------------------------------------
## forFans ###############################################################
----------------------------------------------------------------------- */
#forFans {
	.sectionInner {
		padding: 32px 64px;
	}
	.forFans{
		&_bnr {
			margin: 16px 0 0;
		}
		&_event {
			margin: 16px 0 0;
		}
	}
	.forFansList {
		&_date {
			float: left;
		}
		&_text {
			padding-left: 7em;
		}
	}
}

@media screen and (max-width: 768px){
	#forFans {
		.sectionInner {
			padding: 16px 10px 32px;
		}
		.forFans{
			&_bnr {
				margin: 16px 0 0;
				img {
					width: 100%;
				}
			}
		}
		.forFansList {
			@include fontsize(14);
			&_date {
				display: block;
				float: none;
			}
			&_text {
				padding-left: 0;
			}
		}
	}
}