
/* aside sns */
.sns {
	position: relative;
	background: $color_bg;
	z-index: 2;
	.fb {
		max-width: 500px;
		margin: 64px auto 0;
	}
	.fbWrapper {
		width: 100%;
	}
	.tw {
		width: 1000px;
		height: 3em;
		margin: 32px auto 0;
	}
	.twTicker {
		position: relative;
		width: 90%;
		border: 2px solid #fff;
		border-radius: 5px;
		padding: 8px 10px;
		font-size: .875rem;
		&:before {
			position: absolute;
			top: 0;
			bottom: 0;
			right: -24px;
			width: 0;
			height: 0;
			margin: auto;
			border-style: solid;
			border-width: 8px 0 8px 24px;
			border-color: transparent transparent transparent #fff;
			content: "";
		}
		&:after {
			position: absolute;
			top: 0;
			bottom: 0;
			right: -18px;
			width: 0;
			height: 0;
			margin: auto;
			border-style: solid;
			border-width: 6px 0 6px 18px;
			border-color: transparent transparent transparent #281f1c;
			content: "";
		}
	}
	.charactor {
		position: absolute;
		top: 0;
		bottom: 0;
		right: -70px;
		height: 50px;
		margin: auto;
	}
}

@media screen and (max-width: 768px){
	.sns {
		.tw {
			width: auto;
			height: 10em;
			padding: 0 10px;
		}
		.twTicker {
			width: 80%;
		}
	}
}