/* -----------------------------------------------------------------------
## Faq ###################################################################
----------------------------------------------------------------------- */
#faq {
	.faqList {
		&_question {
			margin-top: 1em;
			padding: 1em 0 0;
			padding-left: 2em;
			border-top: 1px dotted $color_text;
			text-indent: -2em;
			&:before {
				display: inline-block;
				line-height: 1.4;
				width: 1.5em;
				height: 1.5em;
				margin-right: 8px;
				content: "Q";
				background: $color_bg_light;
				border-radius: 1000px;
				text-indent: 0;
				color: $color_text_dark;
				text-align: center;
				font-family: 'Archivo Black', sans-serif;
			}
		}
		&_answer {
			text-indent: -2em;
			margin-top: 1em;
			padding-left: 2em;
			&:before {
				display: inline-block;
				line-height: 1.4;
				width: 1.5em;
				height: 1.5em;
				margin-right: 8px;
				content: "A";
				background: $color_bg_light;
				border-radius: 1000px;
				text-indent: 0;
				color: $color_text_dark;
				text-align: center;
				font-family: 'Archivo Black', sans-serif;
			}
		}
	}
}

@media screen and (max-width: 768px){
	#faq {
		.faqList {
			@include fontsize(13);
			&_question {
				position: relative;
				padding-right: 1.5em;
				text-indent: -2.5em;
				padding-left: 2.5em;
				&:before {
					@include fontsize(16);
				}
				&:after {
					position: absolute;
					top: 0;
					bottom: 0;
					right: 0;
					width: 1em;
					height: 1em;
					line-height: 1;
					margin: auto;
					display: inline-block;
					content: "\f107";
					text-indent: 0;
					font-family: FontAwesome;
					text-align: center;
					@include fontsize(18);
					transition: transform .4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
				}
				&.open:after {
					transform: rotate(180deg);
				}
			}
			&_answer {
				text-indent: -2.5em;
				padding-left: 2.5em;
				&:before {
					@include fontsize(16);
				}
			}
		}
	}
}