/* -----------------------------------------------------------------------
## Ticket ################################################################
----------------------------------------------------------------------- */
#ticket {
	[v-cloak] {
		opacity: 0;
	}
	.calenderWrapper {
		margin-top: 16px;
		text-align: center;
		.pc {
			display: inline-block;
		}
		.sp {
			display: none;
		}
	}
	/* schedule table */
	.calenderWrapper {
		.update {
			text-align: right;
		}
		.calender {
			width: 100%;
			margin-top: 64px;
			&_month {
				@include fontsize(32);
				font-weight: bold;
			}
			&_head {
				border-top: 1px solid $color_line;
				border-left: 1px solid $color_line;
				font-weight: bold;
			}
			&_body {
				border-left: 1px solid $color_line;
			}
			li {
				width: 14.285%;
				float: left;
				border-bottom: 1px solid $color_line;
				border-right: 1px solid $color_line;
			}
			.day {
				display: block;
				padding: 8px;
				font-weight: bold;
				@include fontsize(16);
			}
			.matinee, .soiree {
				display: block;
				padding: 4px 8px;
			}
			.discount {
				background: #9dd7f5;
			}
			.kids {
				background: #f7c7db;
			}
			.inner {
				display: block;
				padding: 4px 8px;
			}
			.haveToSpace {
				padding: 0 8px;
				background: #3fb143;
				border-radius: 1000px;
			}
			.few {
				padding: 0 8px;
				background: #f6aa07;
				border-radius: 1000px;
			}
			.noRoom {
				padding: 0 8px;
				background: #e5001a;
				border-radius: 1000px;
			}
			.full {
				padding: 0 8px;
				background: #c8c8c9;
				border-radius: 1000px;
			}
		}
	}

	.legend {
		margin-top: 4px;
		margin-left: -8px;
		padding: 0 32px;
		text-align: center;
		@include fontsize(14);
		li {
			display: inline-block;
			list-style: none;
			margin-left: 8px;
		}
		.kids {
			padding: 4px;
			background: #f7c7db;
			color: $color_text_dark;
		}
		.daytime {
			padding: 4px;
			background: #9dd7f5;
			color: $color_text_dark;
		}
		.haveToSpace {
			color: #3fb143;
			@include fontsize(18);
		}
		.few {
			color: #f6aa07;
			@include fontsize(18);
		}
		.noRoom {
			color: #e5001a;
			@include fontsize(18);
		}
		.full {
			color: #c8c8c9;
			@include fontsize(18);
		}
	}
	.castSchedule {
		margin-top: 64px;
		&_inner {
			display: flex;
			margin-top: 32px;
			padding: 0 32px;
		}
	}
	.nextStage {
		width: 40%;
		text-align: center;
		&_title {
			@include fontsize(24);
			font-family: 'Archivo Black', sans-serif;
		}
		&_date {
			margin-top: 16px;
			font-size: 7rem;
			line-height: 1;
			font-weight: bold;
			letter-spacing: -5px;
			.weeks {
				display: block;
				margin-top: 16px;
				letter-spacing: .25em;
				@include fontsize(32);
				font-weight: normal;
				font-family: 'Archivo Black', sans-serif;
			}
		}
		.otherBtn a {
			display: block;
			width: 150px;
			height: 150px;
			margin: 64px auto 0;
			line-height: 150px;
			border: 2px solid #fff;
			border-radius: 1000px;
			transition: background .6s ease;
			&:hover {
				background: rgba(255,255,255,.5);
			}
		}
	}
	.nextCast {
		width: 60%;
		&List {
			display: flex;
			align-items: center;
		}
		&_genre {
			width: 7em;
			line-height: 1;
			text-align: center;
			@include fontsize(24);
			font-weight: bold;
			font-family: 'Archivo Black', sans-serif;
		}
		&_name {
			flex: 1;
			line-height: 1;
			margin-top: 8px;
			padding: 10px;
			@include fontsize(24);
			background: #000;
			text-align: left;
			.name {
				margin-left: 8px;
			}
		}
		.mime {
			.nextCast_name {
				background: $color_mime;
			}
			.nextCast_genre {
				color: $color_mime;
			}
		}
		.breakin {
			.nextCast_name {
				background: $color_breakin;
			}
			.nextCast_genre {
				color: $color_breakin;
			}
		}
		.magic {
			.nextCast_name {
				background: $color_magic;
			}
			.nextCast_genre {
				color: $color_magic;
			}
		}
		.jaggling {
			.nextCast_name {
				background: $color_jaggling;
			}
			.nextCast_genre {
				color: $color_jaggling;
			}
		}
		.dall {
			.nextCast_name {
				background: $color_dall;
			}
			.nextCast_genre {
				color: $color_dall;
			}
		}
	}
	.navi {
		display: flex;
		justify-content: center;
		align-items: center;
		&_item {
			margin-top: 32px;
			padding: 0 16px;
			text-align: center;
			@include fontsize(24);
		}
	}
}


@media screen and (max-width: 768px){
	#ticket {
		.calenderWrapper {
			margin: 0 -10px;
			iframe {
				width: 320px;
				margin: 0 auto;
			}
			.pc {
				display: none;
			}
			.sp {
				display: inline-block;
			}
		}
		.legend {
			padding: 0 10px;
			li {
				list-style: none;
				text-align: left;
				margin: 4px 0 0 4px;
			}
			.kids {
				width: 47%;
				text-align: center;
			}
			.daytime {
				width: 47%;
				text-align: center;
			}
		}
		.navi {
			display: block;
			margin-top: 16px;
			&_item {
				display: block;
				margin-top: 8px;
				padding: 0;
				@include fontsize(18);
			}
			a {
				display: block;
				background: $color_bg_light;
				color: $color_text_dark;
			}
		}
		.castSchedule {
			&_inner {
				display: block;
				padding: 0;
			}
		}
		.nextStage {
			width: auto;
			&_date {
				margin-top: 0;
			}
			.weeks {
				margin-top: 0;
			}
			.otherBtn a {
				display: block;
				width: auto;
				height: 44px;
				margin: 32px auto;
				line-height: 44px;
				background: $color_bg_light;
				color: $color_text_dark;
			}
		}
		.nextCast {
			width: 100%;
			margin-top: 32px;
			overflow: hidden;
			&List {
				display: block;
				width: 49%;
				float: left;
				margin-top: 16px;
				&:nth-child(even){
					float: right;
				}
			}
			&_genre {
				width: auto;
				line-height: 1;
				text-align: center;
				@include fontsize(20);
				font-weight: bold;
				font-family: 'Archivo Black', sans-serif;
			}
			&_name {
				display: block;
				margin-top: 4px;
				padding: 10px;
				@include fontsize(18);
				background: #000;
				img {
					width: 100%;
				}
				.name {
					display: block;
					margin-top: 8px;
					margin-left: 0;
					text-align: center;
				}
			}
		}
	}
}