/* -----------------------------------------------------------------------
## About #################################################################
----------------------------------------------------------------------- */
#about {
	.visualBox_copy {
		text-align: center;
		.sp {
			display: none;
		}
	}
	.characterBox {
		display: flex;
		width: 100%;
		height: 500px;
		overflow: hidden;
		margin-top: 16px;
		&_item {
			position: relative;
			display: flex;
			width: 20%;
			text-align: center;
			transition: all .75s $easeOutQuint;
			cursor: pointer;
			overflow: hidden;
			&.active {
				width: 50%;
			}
			&.inactive {
				width: 12.5%;
			}
		}
		&_img {
			position: relative;
			z-index: 2;
			width: 218px;
			overflow: hidden;
			img {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
			}
		}
		&_text {
			position: absolute;
			top: 0;
			right: 0;
			width: 218px;
			z-index: 1;
			margin-top: 32px;
			padding: 16px 10px;
			color: $color_text;
			overflow: hidden;
		}
		&_text_genre {
			@include fontsize(20);
			span {
				display: block;
				@include fontsize(14);
				font-family: 'Archivo Black', sans-serif;
			}
		}
		&_text_body {
			margin-top: 32px;
			text-align: left;
			@include fontsize(14);
		}

		.mime {
			background: $color_mime;
		}
		.breakin {
			background: $color_breakin;
		}
		.magic {
			background: $color_magic;
		}
		.jaggling {
			background: $color_jaggling;
		}
		.dall {
			background: $color_dall;
		}
	}
	.pointBox {
		margin-top: 16px;
		&_item {
			max-width: 420px;
			margin-bottom: 32px;
			float: left;
			&:nth-child(even) {
				float: right;
			}
			&:nth-child(3) {
				margin-bottom: 0;
			}
			&:nth-child(4) {
				margin-bottom: 0;
			}
		}
		&_text {
			margin-top: 8px;
			text-align: center;
			@include fontsize(20);
		}
	}
	.castBox {
		display: flex;
		width: 100%;
		min-height: 500px;
		margin-top: 16px;
		align-items: center;
		text-align: center;
		background: url(https://unsplash.it/g/1000/1000) no-repeat center center;
		background-size: cover;
		&_item {
			width: 50%;
			height: 200px;
			text-align: center;
			@include fontsize(20);
		}
		.fa {
			@include fontsize(40);
		}
		a {
			display: block;
			width: 200px;
			margin: 0 auto;
			padding: 50px 0;
			border: 2px solid $color_line;
			border-radius: 1000px;
			transition: background .4s ease;
			background: rgba(0,0,0,.75);
			&:hover {
				background: $color_bg_light;
				color: $color_text_dark;
			}
		}
	}
}
@media screen and (max-width: 768px) {
	#about {
		.visualBox {
			margin-top: 16px;
			padding: 0;
			&_copy {
				@include fontsize(16);
				.sp {
					display: inline;
				}
			}
			&_text {
				@include fontsize(14);
			}
		}
		.slick-next, .slick-prev {
			top: 10px;
		}
		.characterBox {
			display: block;
			height: auto;
			&_item {
				width: auto;
				height: 300px;
				&.active {
					width: auto;
					.characterBox_img {
						width: 50%;
					}
				}
				&.inactive {
					width: auto;
				}
			}
			&_img {
				width: 100%;
				transition: width .4s ease;
			}
			&_text {
				width: 50%;
				margin-top: 0;
				&_genre {
					@include fontsize(16);
				}
				&_body {
					margin-top: 16px;
					@include fontsize(12);
				}
			}
			&_inner {
				width: 100%;
			}
		}
		.pointBox {
			&_item {
				float: none;
				margin-bottom: 16px;
				&:nth-child(even) {
					float: none;
				}
				&:nth-child(3) {
					margin-bottom: 16px;
				}
				&:nth-child(4) {
					margin-bottom: 16px;
				}
			}
			&_img {
				width: auto;
				img {
					width: 100%;
					height: auto;
				}
			}
			&_text {
				@include fontsize(16);
			}
		}
		.castBox {
			display: block;
			min-height: 0;
			&_item {
				width: 100%;
				height: auto;
				margin-bottom: 16px;
			}
			.fa {
				@include fontsize(40);
				margin-right: 8px;
			}
			a {
				width: auto;
				padding: 25px 0;
				border-radius: 0;
				br {
					display: none;
				}
			}
		}
	}
}